<template>
  <div>
    <video id="tcPlayerId" width="414" height="270" preload="auto" playsinline webkit-playsinline x5-playinline></video>
  </div>
</template>

<script>
export default {
data() {
    return {
      tcPlayerId: 'tcPlayerId',
      player: null,
      videoFileid: '',
      videoAppid: '',
    };
},
methods: {
    // 初始化腾讯云播放器
    getVideoLang (fileID, appID) {
      const playerParam = {
        fileID: fileID,
        appID: appID
      }
      this.player = window.TCPlayer(this.tcPlayerId, playerParam)
      console.log(this.player);
    },
},
 mounted() {
        let self = this;
        setTimeout(() => {
        self.videoFileid = '387702303927512899'
        self.videoAppid = '1313199104'
        self.$nextTick(() => {
            self.getVideoLang(self.videoFileid, self.videoAppid)
        })
        }, 200)
    },
    beforeDestroy () {
        this.player.dispose()
    }
}
</script>

